@charset "UTF-8";
/** Feuille de style générale REEZOCAR **/
/** Abstract **/
/** Feuille de style abstracts-dir **/ /** Feuille de style abstracts-dir/variables **/ /** Feuille de style abstracts-dir/variables/colors **/ /** Feuille de style abstract/variables/fonts **/
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700;800&family=Rozha+One&display=swap&family=Fuzzy+Bubbles:wght@400;700&display=swap");
/** Feuille de style abstract/variables/mixins **/
/** General **/
/** Feuille de style general-dir **/ /** Feuille de style general/reset **/
*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

/** Feuille de style general/main **/
html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

.rz {
  background-color: #FFFFFF;
  overflow-x: hidden;
  font-family: Source Sans Pro;
}

* {
  font-family: "Source Sans Pro", sans-serif;
}

/** Feuille de style general/utils **/
button {
  background: transparent;
  border: 0;
}

.text-center {
  text-align: center;
}

/** Atoms **/
/** Feuille de style atoms/atoms-dir **/ /** Feuille de style atoms/txt **/
.rz_txt {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.8px;
  color: #010035;
}
.rz_txt--bold {
  font-weight: 700;
}

/** Feuille de style atoms/title **/
.rz_title--h1 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 45.25px;
  color: #010035;
}
.rz_title--h2 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 36.4px;
  color: #010035;
}
.rz_title--h3 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 22.63px;
  color: #010035;
}

/** Feuille de style atoms/btn **/
.rz_btn {
  display: block;
  padding: 1.5rem 4rem;
  border: 0;
  border-radius: 2.4rem;
  text-decoration: none;
}
.rz_btn--primary {
  color: #FFFFFF;
  background-color: #E2010B;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20.8px;
  color: #FFFFFF;
}

/** Feuille de style atoms/link **/
a.rz_link {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 20.8px;
  color: #010035;
  text-decoration: none;
}
a.rz_link:visited, a.rz_link:hover, a.rz_link:active {
  text-decoration: none;
}

/** Components **/
/** Feuille de style components/components-dir **/ /** Feuille de style components/nav **/
.rz_nav {
  display: flex;
  flex-flow: row nowrap;
}
.rz_nav__item {
  display: flex;
  align-items: center;
  font-weight: 600;
  margin: 0 3rem;
}
.rz_nav__item > .rz_btn {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
  color: #010035;
  padding-right: 0;
}
.rz_nav__item > .rz_btn--call {
  padding: 0.8rem 2rem 0.7rem 4.4rem;
  border-radius: 2.4rem;
  text-align: center;
  position: relative;
}
.rz_nav__item > .rz_btn--call:before {
  content: "";
  background-image: url("../../dist/img/phone.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 1.9rem;
  transform: translateY(-50%);
  width: 1.6rem;
  height: 1.6rem;
}
.rz_nav__item > .rz_btn--call span {
  display: block;
  font-size: 1rem;
  font-style: italic;
}
.rz_nav__item:first-child {
  margin-left: 0;
}
.rz_nav__item:first-child > .rz_btn:first-child {
  position: relative;
}
.rz_nav__item:first-child > .rz_btn:first-child:after {
  display: block;
  content: "";
  background-image: url("../../dist/img/arrow.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -13px;
  width: 13px;
  height: 7px;
}
.rz_nav__item:last-child {
  margin-right: 0;
}
.rz_nav__item:last-child > .rz_btn {
  background-color: #E2010B;
  color: #FFFFFF;
}

/** Feuille de style components/search **/
.rz_search-box__label {
  visibility: hidden;
  position: absolute;
}
.rz_search-box__input {
  border: 1px solid #DCDCDC;
  padding: 1.3rem 1rem 1.2rem;
  width: 100%;
  border-radius: 0.4rem;
}
.rz_search-box__input::placeholder {
  font-family: "Source Sans Pro", sans-serif;
  color: #010035;
  font-size: 1.6rem;
}
.rz_search-box__wrapper {
  position: relative;
  width: 100%;
  margin: 0 auto;
}
.rz_search-box__btn {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  background: transparent;
}

/** Feuille de style components/keypoints **/
.rz_keypoints {
  margin-top: 5.8rem;
  position: relative;
}
.rz_keypoints__wrapper {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
  z-index: 2;
}
.rz_keypoints__wrapper .rz_keypoints__thumbnail,
.rz_keypoints__wrapper .rz_keypoints__content {
  flex-basis: 50%;
}
.rz_keypoints__wrapper .rz_keypoints__content .rz_txt:not(:first-child) {
  margin-top: 2rem;
}
.rz_keypoints__wrapper .rz_keypoints__thumbnail img {
  max-height: 37.5rem;
}
.rz_keypoints__wrapper:nth-child(2) {
  margin-top: 8rem;
  margin-bottom: 11.7rem;
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__content {
  flex-basis: 70%;
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail {
  flex-basis: 30%;
}
.rz_keypoints__wrapper:nth-child(2) .rz_keypoints__thumbnail img {
  max-width: 65rem;
  margin-right: -8rem;
}
.rz_keypoints__link {
  position: absolute;
  z-index: 1;
  top: 18%;
  left: 35%;
  opacity: 0.9;
}
.rz_keypoints__link:last-child {
  top: 55%;
  left: 21%;
}

/** Feuille de style components/push **/
.rz_push {
  display: flex;
  flex-flow: row wrap;
  text-align: center;
  margin-top: 5rem;
}
.rz_push__item {
  padding: 3rem 1.5rem;
  max-width: 25rem;
  width: 100%;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 0.4rem;
  margin-right: 2.5rem;
  margin-bottom: 3.5rem;
}
.rz_push__item:last-child {
  margin-right: 0;
}
.rz_push__content {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.6px;
  color: #010035;
}
.rz_push__thumbnail {
  height: 4rem;
}
.rz_push__link {
  text-decoration: underline;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.6px;
  color: #010035;
}
.rz_push__thumbnail, .rz_push__title {
  margin-bottom: 1.7rem;
}
.rz_push--option {
  flex-wrap: nowrap;
}
.rz_push--option .rz_push__item {
  max-width: inherit;
  flex-basis: 50%;
}
.rz_push--option .rz_push__item .rz_btn {
  margin-top: 2.2rem;
}

/** Layouts **/
/** Feuille de style layouts/layouts-dir **/ /** Feuille de style layouts/header **/
.rz_header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1.6rem 0;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}
.rz_header__wrapper {
  padding: 0 5rem;
}
.rz_header__wrapper:first-child {
  padding-right: 0;
}
.rz_header__wrapper:nth-child(2) {
  flex-grow: 1;
}
.rz_header__wrapper:last-child {
  padding-left: 0;
}

/** Feuille de style layout/main-container **/
.rz_main {
  margin: 7.5rem auto 0 auto;
  max-width: 1400px;
}